@import "../service/media-mixin.scss";

.main-prices {
  padding-top: 7.5rem;
  padding-bottom: 4rem;
  @include max-lg {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
  &__title {
    margin-bottom: 6.25rem;
    @include max-lg {
      margin-bottom: 2.5rem;
    }
  }
  &__cards {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    //margin-bottom: 3.125rem;
    @include max-lg {
      flex-direction: column;
      gap: 0.85rem;
      //margin-bottom: 2.5rem;
    }
  }
  &__card {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 4.5rem;
    width: 34.5rem;
    border-radius: 4rem;
    padding: 2rem 3rem;
    background-color: #c8ff70;
    overflow: hidden;
    @include max-lg {
      width: 100%;
      border-radius: 3rem;
      padding: 1.5rem 1.75rem;
      gap: 1.999rem;
    }
    &--center {
      background-color: #98e51c;
      gap: 7.125rem;
      @include max-lg {
        gap: 3.5rem;
      }
    }
    &-image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      @include max-lg {
        height: 83%;
        top: 2rem;
        left: -1rem;
        width: 175%;
        img {
          height: 133%;
          width: 150%;
        }
      }
    }
    &-top {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      gap: 0.75rem;
    }
    &-title {
      font-family: "TikTok", sans-serif;
      font-weight: 950;
      font-size: 3rem;
      line-height: 2.875rem;
      letter-spacing: -0.06em;
      color: #1d1d20;
      @include max-lg {
        font-size: 1.75rem;
        line-height: 1.6625rem;
        width: 80%;
      }
    }
    &-description {
      font-family: "Golos", sans-serif;
      font-weight: 500;
      font-size: 1rem;
      line-height: 1.25rem;
      color: #1d1d20;
      @include max-lg {
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 1.22875rem;
      }
      ul {
        display: flex;
        flex-direction: column;
        gap: 0.375rem;
      }
    }
    &-prices {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      gap: 3rem;
      @include max-lg {
        gap: 1.4rem;
      }
    }
    &-price {
      display: flex;
      flex-direction: column;
      gap: 0.25rem;
      &-name {
        font-family: "TikTok", sans-serif;
        font-weight: 730;
        font-size: 2.5rem;
        line-height: 2.375rem;
        letter-spacing: -0.066em;
        color: #1d1d20;
        @include max-lg {
          font-weight: 630;
          font-size: 2rem;
          line-height: 2.05rem;
          letter-spacing: -0.083em;
        }
      }
      &-value {
        font-family: "TikTok", sans-serif;
        font-weight: 950;
        font-size: 4rem;
        line-height: 4rem;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        color: #1d1d20;
        white-space: nowrap;
        @include max-lg {
          font-size: 3rem;
          line-height: 2.87rem;
          letter-spacing: -0.08em;
        }
      }
    }
    &-button {
      position: relative;
      z-index: 1;
      @include max-lg {
        margin-top: -0.18rem;
      }
      .button {
        width: 100%;
        font-family: "TikTok", sans-serif;
        font-weight: 950;
        padding: 2rem;
        font-size: 1.75rem;
        line-height: 1.25rem;
        letter-spacing: -0.05em;
        text-transform: uppercase;
        color: #1d1d20;
        &:hover {
          color: rgb(152, 229, 28);
        }
        @include max-lg {
          font-size: 1.25rem;
          line-height: 100%;
          letter-spacing: -0.05em;
          padding: 1.4rem;
        }
      }
    }
  }
  &__button {
    display: flex;
    justify-content: center;
    margin-top: 3rem;
    @include max-lg {
      margin-top: 2.5rem;
      .button {
        width: 100%;
      }
    }
  }
}
